import { mapState, mapGetters } from 'vuex'
import Breadbar from "~/components/Breadbar.vue";
export default {
  name: "homepage",
  components: [ Breadbar ],
  computed: {
    ...mapState(["accessToken"]),
  },
  watch: {
    page () {
      this.getPosts(this.page,'normal',200)
    }
  },
  data() {
    return {
      selectdate: false,
      searchTypeSelect: '',
      pageSize: 200,
      page: 1,
      itemsPerPage: 30,
      totalRecord: 0,
      totalPage: 0,
      loading: true,
      search: '',
      searchType: [
        { text: 'uuid', value: 'uuid' },
        { text: 'Display Name', value: 'displayName' },
      ],
      dates: '',
      posts: [],
      headers: [
        { text: 'ID', align: 'start', value: 'id' },
        { text: 'Picture Preview', value: 'profileImage' },
        { text: 'Posted Name', value: 'displayName' },
        { text: 'Post Text', value: 'caption' },
        { text: 'Likes', value: 'likeAmount' },
        { text: 'Comments', value: 'commentAmount' },
        { text: 'Post Date', value: 'createdAt' },
        { text: 'Status', value: 'hidden' },
        { text: 'Action', value: 'function' },
      ],
    }
  },
  mounted () {
    this.getPosts(1,'normal',200)
  },
  methods: {
    async setPublish (data) {
      alert(data.isPublished)
      data.loading = true
      const dataset = {
        isPublished: data.isPublished,
        postId: data.id
      }
      const headers = {
        "x-rotor-auth": this.accessToken
      }
      await this.$axios.$post('/v1/posts.change-published', dataset, { headers }).then((response) => {
        data.loading = false
      }).catch((error) => {
        console.log(error)
        data.loading = false
      })
      data.loading = false
    },
    searchData () {
      if (this.search != '' && this.searchTypeSelect != '') {
        this.getPosts(1,'search',200)
      } else {
        this.getPosts(1,'normal',200)
      }
    },
    totalPageArr (totalPage) {
      let data = []
      for (var i = 1; i <= totalPage; i++) {
        data.push(i)
      }
      return data
    },
    dateget (text) {
      if (text) {
        var newtext = text.split("T")
        var newtext = newtext[1].split(".")
        return newtext[0]
      } else {
        return '-'
      }
    },
    timeget (text) {
      if (text) {
        var newtext = text.split("T")
        return newtext[0]
      } else {
        return '-'
      }
    },
    ectext (text) {
      if (text) {
        if (text.length > 8) {
            return text.slice(0,8)+'...'
        } else {
            return text
        }
      } else {
        return "-"
      }
    },
    getPosts(pageSelect,status,pageSize) {
      this.posts = []
      this.loading = true
      let apiurl
      if (status == 'normal') {
        apiurl = '/v1/posts.all?page='+pageSelect+'&pageSize=200'
      } else {
        apiurl = '/v1/posts.all?page='+pageSelect+'&pageSize='+pageSize+'&'+this.searchTypeSelect+'='+this.search
      }
      this.$axios.$get(apiurl, {
        headers: {"x-rotor-auth": this.accessToken}
      }).then((response) => {
        console.log(response)
        this.loading = false
        if (response.data && response.data != null) {
          this.posts = response.data
          if (status == 'normal') {
            this.totalRecord = response['_metadata'].totalRecords
            this.totalPage = response['_metadata'].totalPages
          } else {
            this.totalRecord = this.posts.length
            this.totalPage = Math.ceil(this.posts.length / 200)
          }
        } else {
          this.posts = []
          this.totalRecord = 0
          this.totalPage = 1
        }
      }).catch((error) => {
        this.loading = false
      })
    },
    viewRoute(link) {
      this.$router.push({
        path: link
      });
    },
    redirectTo(path) {
      this.$router.replace({ path });
    }
  }
};
